body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  color: #000000;
  background-color: #ffffff;
  width: 320px;
  margin: 0 auto;
  text-align: center;
  text-transform: uppercase;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header {
  position: absolute;
  width: 320px;
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.main-nav {
  display: flex;
  flex-direction: column;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.main-nav__toggle {
  width: 23px;
  height: 22px;
  position: absolute;
  border: 0;
  padding: 0;
  top: 17px;
  right: 20px;
}
.site-list__item {
  position: relative;
  width: 320px;
  height: 55px;
  background-color: #81b3d2;
  margin-bottom: 1px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
.site-list__link {
  position: relative;
  display: block;
  font-family: "PT Sans", sans-serif;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  padding-top: 17px;
  padding-bottom: 12px;
  text-transform: uppercase;
}
.feedback-list__item {
  position: relative;
  width: 320px;
  height: 55px;
  background-color: #81b3d2;
  margin-bottom: 1px;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
.feedback-list__link {
  position: relative;
  display: block;
  font-family: "PT Sans", sans-serif;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  padding-top: 17px;
  padding-bottom: 12px;
  text-transform: uppercase;
}
.main {
  flex-grow: 1;
}
.main__slogan {
  display: flex;
  flex-direction: column;
  color: #ffffff;
  background-color: #000000;
  background-image: url("../img/intro/slogan-background.jpg");
  background-size: 320px 389px;
  background-repeat: no-repeat;
}
.main__introduction {
  display: flex;
  flex-direction: column;
}
.main__attractions {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  justify-content: center;
  list-style-type: none;
}
.main__search-hotel {
  display: flex;
  flex-direction: column;
}
.slogan__logo {
  width: 282px;
  height: 214px;
  margin: 90px 19px 85px 19px;
}
.slogan__logo::after {
  content: "";
  display: block;
  margin-top: auto;
  background-image: url("../img/intro/welcome-decoration.svg");
  width: 100%;
  height: 57px;
  background-repeat: no-repeat;
}
.introduction__title {
  width: 282px;
  height: 80px;
  margin-top: 32px;
  margin-left: 18px;
  margin-bottom: 35px;
  font-size: 18px;
}
.introduction__description {
  width: 282px;
  height: 42px;
  color: #333333;
  margin: 0;
  margin-left: 18px;
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.attractions__attraction {
  background-color: #eeeeee;
}
.attractions__attraction--deployed {
  color: #ffffff;
  background-color: #81b3d2;
}
.attraction__title {
  font-size: 18px;
}
.attraction__number,
.attraction__description {
  font-size: 14px;
  line-height: 21px;
}
.attraction__advantages {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: #000000;
  background-color: #ffffff;
}
.advantages__title {
  font-size: 18px;
}
.advantages__description {
  color: #333333;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.search-hotel__title {
  font-size: 21px;
}
.search-hotel__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.search-hotel__button {
  width: 284px;
  height: 58px;
  color: #ffffff;
  background-color: #81b3d2;
  border: 0;
  margin-bottom: 49px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
.footer__hashtag {
  font-size: 21px;
}
.footer__social {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
  justify-content: center;
  gap: 6px;
}
.social__item {
  width: 48px;
  height: 48px;
  background-color: #81b3d2;
}
.copyright {
  display: flex;
  justify-content: center;
}
.copyright__developed {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.copyright__logo {
  align-self: center;
}
body {
  background: #ff0000;
}
